import React from 'react';
import TableContainer from "@mui/material/TableContainer";
import Paper from "@mui/material/Paper";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TableCell from "@mui/material/TableCell";
import TableBody from "@mui/material/TableBody";
import {useAppSelector} from "hooks/hooks";
import {selectCardPacks} from "store/selectors/selectPacks";
import {Table} from "@mui/material";
import s from "pages/packs/Packs.module.css";
import {selectCards} from "store/selectors/selectCards";
import {CardsActions} from "pages/packs/cards/components/tables/CardsActions/CardsActions";

export const CardsTable = () => {

    const cards = useAppSelector(selectCards)

    return (
        <div className={s.tableContainer}>
            <TableContainer component={Paper}>
                <Table size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Question</TableCell>
                            <TableCell>Answer</TableCell>
                            <TableCell>Last Updated</TableCell>
                            <TableCell>Grade</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {cards.map((card) => (
                            <TableRow key={card._id}>
                                <TableCell component="th" scope="row">
                                    {card.question}
                                </TableCell>
                                <TableCell>{card.answer}</TableCell>
                                <TableCell>{card.updated ? new Date(card.updated).toLocaleString('en-US', {
                                    year: 'numeric',
                                    month: 'long',
                                    day: 'numeric',
                                }) : ''}</TableCell>
                                <TableCell>{card.grade}</TableCell>
                                {/* card actions */}
                                <TableCell><CardsActions cardId = {card._id}/></TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
};